import React from 'react';
import usePageTitle from "../hooks/usePageTitle";
import {OrganizationCarousel, HeroCarousel, TeamSection} from "../components";
import {PresentationSection, Whyus} from "../components";
import HeroBg from "../assets/images/background/herobg.jpg";
import routes from '../routes/routes';

const About = () => {
    usePageTitle('Qui sommes-nous ?');

    const heroSlides = [
        {
            title: "Une passion commune devenue ralit",
            description: "Ne dune passion commune qui a anim quelques condisciples, Marolook a vu le jour en lanne 2018.",
            backgroundImage: HeroBg,
            buttons: [
                {
                    text: "Nous rejoindre",
                    path: routes.contact,
                    variant: "secondary"
                }
            ]
        },
        {
            title: "Un chemin parsem dpreuves",
            description: "Non sans difficult, chaque tape a renforc notre passion, comptences et expertise.",
            backgroundImage: HeroBg,
        },
        {
            title: "Rpondre aux besoins de notre clientle",
            description: "Nous nous efforons de rpondre au mieux aux attentes de notre clientle avec engagement et dvouement.",
            backgroundImage: HeroBg,
            buttons: [
                {
                    text: "Nos services",
                    path: routes.about,
                    variant: "primary"
                },
                {
                    text: "Voir nos ralisations",
                    path: routes.portfolio,
                    variant: "secondary"
                }
            ]
        }
    ];

    return (
        <div className="flex flex-col overflow-x-hidden">
            <HeroCarousel
                slides={heroSlides}
                autoPlayInterval={1000}
                height="h-[700px]"
                showArrows={true}
                showDots={true}
            />

            <PresentationSection />

            <OrganizationCarousel/>

            <Whyus />

            <TeamSection />
        </div>
    );
};

export default About;