import React from 'react';
import usePageTitle from "../hooks/usePageTitle";
import {HeroCarousel, ServiceSection} from "../components";
import HeroBg from "../assets/images/background/herobg.jpg";
import routes from '../routes/routes';

const Service = () => {
    usePageTitle('Nos service');

    const heroSlides = [
        {
            title: "Capturez vos moments inoubliables",
            description: "De la photographie  la vidographie, nous immortalisons vos souvenirs les plus prcieux.",
            backgroundImage: HeroBg,
            buttons: [
                {
                    text: "Contactez-nous",
                    path: routes.contact,
                    variant: "secondary"
                }
            ]
        },
        {
            title: "Sublimez votre image",
            description: "Grce  notre expertise en design graphique et animation 3D, donnez vie  vos ides.",
            backgroundImage: HeroBg,
            buttons: [
                {
                    text: "Voir nos ralisations",
                    path: routes.portfolio,
                    variant: "primary"
                },
                {
                    text: "Demander un devis",
                    path: routes.contact,
                    variant: "secondary"
                }
            ]
        },
        {
            title: "Dveloppez votre prsence en ligne",
            description: "Avec notre community management et dveloppement web, touchez un public international.",
            backgroundImage: HeroBg,
        },
        {
            title: "Organisez des vnements mmorables",
            description: "Faites de vos occasions spciales des souvenirs inoubliables grce  notre expertise en organisation vnementielle et dcoration.",
            backgroundImage: HeroBg,
        }
    ];


    return (
        <div className="flex flex-col">
            <HeroCarousel
                slides={heroSlides}
                autoPlayInterval={1000}
                height="h-[700px]"
                showArrows={true}
                showDots={true}
            />

            <ServiceSection />
        </div>
    );
};

export default Service;